import uniStatusBar from "../../components/uni-status-bar/uni-status-bar.vue";
import otherLogin from "../../components/home/other-login.vue";
export default {
	components:{
		uniStatusBar,
		otherLogin
	},
	data() {
		return {
			status:false,//false代表邮箱、密码登录页面，true代表注册页面
			disabled:true,
			loading:false,
			mail:"",
			password:"",
			codetime:0,
			}
	},
	watch:{
		useremail(val){
			this.OnBtnChange();
		},
		password(val){
			this.OnBtnChange();
		}
	},
	methods: {
		// 验证手机号码
		email: function() {
		      var email = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
		      if (!email.test(this.mail)) {
		        uni.showToast({title: '邮箱格式错误！',icon: 'none'});		
		        this.mail = "";
		      }
		    },
		mima: function() {
		      var password = /^(?![a-zA-z]+$)(?!\d+$){6,16}$/;
		      if (!password.test(this.password)) {
		        uni.showToast({title: '密码长度6-16，必须由数字和字母组成！',icon: 'none'});		
		        this.password = "";
		      }
		    },
		},
		// 初始化表单
		initInput(){
			this.useremail='';
			this.password='';
		},
		// 改变按钮状态
		OnBtnChange(){
			if(this.useremail && this.password){
				this.disabled=false; return;
			}
			this.disabled=true;
		},
		// 切换登录状态
		changeStatus(){
			this.initInput();
			this.status = !this.status;
		},
		// 返回上一步
		back(){
			uni.navigateBack({ delta: 1 });
		},
		// 提交登录
		submit(){
			// 账号密码登录
			if(!this.status){
				return this.User.login({
					url:"/user/login",
					data:{
						username:this.useremail,
						password:this.password
					}
				})
			}
			// 验证码登录
			// 验证手机号合法性
			if(!this.isEmail()){
				return uni.showToast({ title: '请输入正确的邮箱', icon:"none" });
			}
			
	}
}
